import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Header = styled.div`
  font-size: 24px;
  font-weight: bold;
`;

const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
`;

const Th = styled.th`
  font-size: 17px;
  font-weight: bold;
`;

const Td = styled.td`
  padding: 8px;
  text-align: center;
`;

const Ad = styled.div`
  margin-top: 20px;
`;

function Home() {
  const [data, setData] = useState(null);

  useEffect(() => {
    const json_url = 'https://public-piece-web.s3.amazonaws.com/json/price.json?{%22srchDt%22%20:%20%22TODAY%22,%22type%20:%20%22Au%22}';
    fetch(json_url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(`HTTP Error: ${response.status}`);
        }
      })
      .then((jsonData) => {
        setData(jsonData.lineUpVal[0]);
      })
      .catch((error) => {
        console.error('    :', error);
      });
  }, []);

  return (
    <Wrapper>
      <Header> </Header>
      <Table className="main_market_table">
        <thead>
          <tr>
            <Th>3.75g</Th>
            <Th> </Th>
            <Th> </Th>
          </tr>
        </thead>
        <tbody className="default">
          <tr>
            <th></th>
            <Td id="spure">{data && data.spure.toLocaleString() + ''}</Td>
            <Td id="ppure">{data && data.ppure.toLocaleString() + ''}</Td>
          </tr>
          <tr>
            <th>18K</th>
            <Td></Td>
            <Td id="p18k">{data && data.p18k.toLocaleString() + ''}</Td>
          </tr>
          <tr>
            <th>14K</th>
            <Td></Td>
            <Td id="p14k">{data && data.p14k.toLocaleString() + ''}</Td>
          </tr>
          <tr>
            <th></th>
            <Td id="swhite">{data && data.swhite.toLocaleString() + ''}</Td>
            <Td id="pwhite">{data && data.pwhite.toLocaleString() + ''}</Td>
          </tr>
          <tr>
            <th></th>
            <Td id="ssilver">{data && data.ssilver.toLocaleString() + ''}</Td>
            <Td id="psilver">{data && data.psilver.toLocaleString() + ''}</Td>
          </tr>
        </tbody>
      </Table>
      <Ad></Ad>
      <div></div>
    </Wrapper>
  );
}

export default Home;