import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useLocation } from 'react-router-dom';

//   
const ProductList = styled.ul`
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
`;

const ProductItem = styled.li`
  width: 250px;
  margin: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
`;

const ProductImage = styled.img`
  max-width: 100%;
  height: auto;
`;

const ProductTitle = styled.h3`
  font-size: 1.2em;
  margin: 10px 0;
`;

const ProductPrice = styled.p`
  font-size: 1.1em;
  color: #007acc;
`;

const PriceChangeBadge = styled.span`
  color: red;
  font-weight: bold;
  margin-right: 4px;
`;

const AddToCartButton = styled.button`
  background: #007acc;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
`;

function Gift() {

    const [productData, setProductData] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch(`https://minvis.eu.pythonanywhere.com/show_gift`);
                if (response.ok) {
                    const productData = await response.json();
                    setProductData(productData);
                } else {
                    alert('  .');
                    window.location.reload();
                }
            } catch (error) {
                console.error(' :', error);
                window.location.reload();
            }
        };

        fetchData();
    }, []);

    const addCart = async (product_id) => {
        const jwtToken = localStorage.getItem('jwt_token');
        
        //   
        const isConfirmed = window.confirm("   ?    !");
    
        if (isConfirmed) {
            try {
                const response = await fetch(
                    `https://minvis.eu.pythonanywhere.com/purchase?client_jwt_token=${jwtToken}&product_id=${product_id}`,
                    { method: 'GET' }
                );
                if (response.ok) {
                    const result = await response.json();
                    alert(result.message);
                    //      
                    //       .
                } else {
                    alert('    .');
                    window.location.reload();
                }
            } catch (error) {
                console.error(' :', error);
                window.location.reload();
            }
        }
    };

    return (
        <ProductList>
            {productData.map((product) => {
                return (
                    <ProductItem key={product.product_id}>
                            <ProductImage src={product.img_src} alt={product.title} />
                            <ProductTitle>{product.title}</ProductTitle>
                            <ProductPrice>
                                {product.price.toLocaleString()}
                            </ProductPrice>
                        <AddToCartButton onClick={() => addCart(product.product_id)}>
                            
                        </AddToCartButton>
                    </ProductItem>
                );
            })}
        </ProductList>
    );
}

export default Gift;