import React, { useEffect, useState } from 'react';
import KakaoLogin from "react-kakao-login";
import styled from 'styled-components';
import jwt_decode from 'jwt-decode'; // jwt-decode  
import { Link } from 'react-router-dom'; // Link 

const StyledSocialKakao = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  font-family: Arial, sans-serif;
`;

const UserInfo = styled.div`
  font-size: 18px;
  margin-bottom: 10px;
`;

const LogoutButton = styled.button`
  background-color: #ff5722;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  margin-bottom: 10px;
  border-radius: 5px;
`;

const SocialKakao = () => {
  const kakaoClientId = 'f2156d142c51785dc9597e7d98b0a0bd';
  const [userInfo, setUserInfo] = useState(null); //    

  useEffect(() => {
    const jwtToken = localStorage.getItem('jwt_token'); // localStorage jwt_token 
    // jwt        
    if (localStorage.getItem('jwt_token')) {
      const decodedToken = jwt_decode(jwtToken); // jwt  
      if (decodedToken) {
        const userEmail = decodedToken.user_email; //   
        setUserInfo({ user_email: userEmail });

        // JWT    
        const expirationTime = decodedToken.exp * 1000; //      
        const currentTime = Date.now();
        if (expirationTime < currentTime) {
          //    
          localStorage.removeItem('jwt_token');
          setUserInfo(null);
        }
      }
    }
  }, []); // jwtToken   

  const kakaoOnSuccess = async (data) => {
    const access_token = data.response.access_token;

    const serverURL = 'https://minvis.eu.pythonanywhere.com/user';
    const queryParams = `token=${access_token}`;

    try {
      const response = await fetch(`${serverURL}?${queryParams}`, {
        method: 'GET',
      });

      if (response.ok) {
        const json_token = await response.json();
        const jwt_token = json_token["jwt_token"];
        const decodedToken = jwt_decode(jwt_token); // jwt  
        if (decodedToken) {
          const userEmail = decodedToken.user_email; //   
          setUserInfo({ user_email: userEmail });
        }
        localStorage.setItem('jwt_token', jwt_token); // localStorage  
        //           .
      } else {
        console.error('  :', response.status, response.statusText);
      }
    } catch (error) {
      console.error(' :', error);
    }
  }

  const kakaoOnFailure = (error) => {
    console.log(error);
  };

  const handleLogout = () => {
    localStorage.clear();
    setUserInfo(null); //   
  };

  return (
    <StyledSocialKakao>
      {userInfo ? (
        <>
          <LogoutButton onClick={handleLogout}></LogoutButton>
          {userInfo && <UserInfo>{userInfo.user_email}</UserInfo>}
          <Link to="/mypage" style={{ textDecoration: 'none' }}></Link>
        </>
      ) : (
        <KakaoLogin
          token={kakaoClientId}
          onSuccess={kakaoOnSuccess}
          onFail={kakaoOnFailure}
        />
      )}
    </StyledSocialKakao>
  );
}

export default SocialKakao;
